import React from "react";
import { Card } from "./Card";
import { Swiper, SwiperSlide } from "swiper/react";
import { Tabs } from "./Tabs";
import s from "./Weather.module.scss";
import "swiper/css";
import { Weather } from "../../../../store/types/types";

interface Props {
  weather: Weather;
}

export interface FullInfoDay {
  specification: string;
  day_info: string;
  icon_id: string;
  temp_day: string;
  temp_night: string;
  info: string;
}

export const WeatherInfo = ({ weather }: Props) => {
  const unixTime = weather.dt;
  const date = new Date(unixTime * 1000);
  const MONTHS = [
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
  ];
  const WeekDate = `${date.getDate()} ${MONTHS[date.getMonth()]}`;

  const weatherInfos: FullInfoDay[] = [
    {
      specification: "",
      day_info: WeekDate,
      icon_id: "water",
      temp_day: `${weather.main.humidity}%`,
      temp_night: "",
      info: "",
    },
    {
      specification: "",
      day_info: WeekDate,
      icon_id: "wind",
      temp_day: `${weather.wind.speed} /`,
      temp_night: "",
      info: "",
    },
    {
      specification: "",
      day_info: WeekDate,
      icon_id: "eye",
      temp_day: `${weather.visibility} .`,
      temp_night: "",
      info: "",
    },
    {
      specification: " ",
      day_info: WeekDate,
      icon_id: "temp",
      temp_day: `${weather.main.temp_max} C`,
      temp_night: "",
      info: "",
    },
    {
      specification: " ",
      day_info: WeekDate,
      icon_id: "temp",
      temp_day: `${weather.main.temp_min} C`,
      temp_night: "",
      info: "",
    },
    {
      specification: "",
      day_info: WeekDate,
      icon_id: "mainly_cloudy",
      temp_day: `${weather.clouds.all}%`,
      temp_night: "",
      info: "",
    },
    {
      specification: "",
      day_info: WeekDate,
      icon_id: "barometer",
      temp_day: `${weather.main.pressure} ..`,
      temp_night: "",
      info: "",
    },
  ];

  return (
    <>
      <Tabs />
      <div className={s.days}>
        <Swiper
          spaceBetween={18}
          slidesPerView={`auto`}
          slidesPerGroupAuto
          className={s.swiper}
        >
          {weatherInfos.map((info: FullInfoDay) => (
            <SwiperSlide className={s.swiperItem} key={info.specification}>
              <Card info={info} />
            </SwiperSlide>
          ))}
        </Swiper>
      </div>
    </>
  );
};
